import React, { Component } from 'react';
import { Container, Row, Col } from 'react-grid-system'
import check from '../assets/icons/tick (2)-01.svg'
import Card from 'react-bootstrap/Card'

class YoureTrapped extends Component {
  state = { 
    text:[
    "What if you knew how to apply the right high-level frameworks to yourself and your teams that are accessible, repeatable, expandable AND updatable, even when there's no silver bullet?",
    'What if you discovered the science behind mastering softer “human” skills instead of resorting to being a hard-ass to dictate your team’s outcomes?',
    "What if you had access to perspectives about how tech and digitization will change the way you interact with people, how you learn, how you work and how you run organizations?",
    "What if you could do all this so you don’t have to regularly attend therapy, and Without the cost being your health?",
    "And without having to consider leaving because you feel afraid and unsafe, and without quietly struggling to meet the expectations of your role behind the scenes.",
    "Even when there’s no company mentor to guide you in the process and without you having to pay a fortune for your own private coach."
    ] 
  }
  render() { 
    return ( 
      <Container fluid>
        <Row className="trappel" id="trappel">
          <h2 className="caligrafia">
            So you're trapped.
          </h2>
          <Col md={12} className="container-text-trapped">
            <p className="text-trapped">
            It’s NOT time for more rapid-learning techniques to help you cram more knowledge into fewer hours. Because we both know knowledge isn’t what you’re missing right now.
            </p>
            <p className="text-trapped">
            It’s NOT time for more people management theories to help you put in place even more systems and processes.  Because that’s what got you into this mess in the first place.
            </p>
            <p className="text-trapped">
            And it’s NOT time for the latest mindfulness hacks to help you “center” and get more productive.  
            Because learning yoga won’t give you the strategies you need.
            </p>
          </Col>
          <Col md={12}>
            <h2 className="caligrafia title-cards-trapped">
            What if there was another way?
            </h2>
            {
            this.state.text.map((data)=>{
              return(
                <Row justify="center">
                <Col md={9}>
                  <Card>
                    <Card.Body style={{display:'flex'}}>
                      <img src={check} alt="check" className="check"/>
                      <div className="data-card">{data}</div>
                    </Card.Body>
                  </Card>
                  <br/>
                </Col>
                </Row>
              )
            })
          } 
          </Col>
        </Row>
      </Container>
     );
  }
}
 
export default YoureTrapped;